
import { defineComponent } from 'vue'
import { Option } from '../Forms/FieldInterface'
import SelectMixin from "@/components/FormElements/SelectMixin.vue"
import { find } from "lodash"

export default defineComponent({
    name: "HisSelect",
    mixins: [SelectMixin],
    data: () => ({
        isInit: false as boolean
    }),
    mounted() {
        this.init()
    },
    activated() {
        this.init()    
    },
    methods: {
        clearValue() { 
            this.clearSelection() 
        },
        async init() {
            this.$emit('onFieldActivated', this)
            this.listData = await this.options(this.fdata)
            if (!this.isInit) {
                await this.setDefaultValue()
            } else {
                if (typeof this.config.onInitValue === 'function') {
                    const defaults: string = await this.config.onInitValue(this.selected, this.fdata, this.cdata)
                    if (defaults) {
                        const found = find(this.listData, {label: defaults}) || find(this.listData, {value: defaults}) 
                        if (found) {
                            this.onselect(found)
                        } else {
                            this.selected = defaults
                            this.filter = defaults
                        }
                    } else {
                        this.onselect(null)
                        this.filter = ''
                    }
                }
            }
            this.isInit = true
        },
        async setDefaultValue() {
            if(this.defaultValue) {
                const defaults: string = await this.defaultValue(this.fdata, this.cdata, this.selected)
                if (defaults) {
                    const found = find(this.listData, {label: defaults}) || find(this.listData, {value: defaults}) 
                    if (found) {
                        this.onselect(found)
                    } else {
                        this.selected = defaults
                        this.filter = defaults
                    }
                }
            }
        },
        async onselect(item: Option | null) {
            if (item) {
                this.selected = item.label
            } else {
                this.selected = ''
            }
            if (this.onValue) {
                const ok = await this.onValue(item, this)
                if (!ok) {
                    this.selected = ''
                    return
                }
            }
            this.$emit('onValue', item)
        }
    }
})
